.carousel-inner {
    border-radius: 15px;
}

.carousel-m {
    height: 250px;
}

.events-header {
    padding-top: 2.5%;
    padding-bottom: 2.5%;
}

.events-header-m {
    padding-top: 50px;
    padding-bottom: 20px;
}

.events-today {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 10%;
}
.events-today-m {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.p-events-today {
    font-family:Gilroy-Extrabold ;
    letter-spacing: 2px;
    color: #304F76;
    text-transform: uppercase;
    padding-right: 5px;
    position: relative;
    font-size: 1rem;
}

.p-events-today::after {
    content: "";
    display: block;
    width: 55%;
    height: 1px;
    background: #a3a3a377;
    right: 0;
    top: 40%;
    position: absolute;
}

.p-events-today-m {
    margin-top: 25px;
    margin-bottom: 40px;
    position: relative;
}

.p-events-today-m::after {
    content: "";
    display: block;
    width: 65%;
    height: 1px;
    background: #a3a3a377;
    right: 0;
    top: 40%;
    position: absolute;
}

.eventtoday{
    width: 65px;
}

.event-info{
    font-family:Gilroy-Light;
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    font-weight: bold;
}
.one {
    display: flex;
    padding-top: 5%;
    align-items: center;
}

.one-m{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.two {
    display: flex;
    padding-top: 20%;
    align-items: center;
}

.two-m{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.carousel-item-m {
    height: 250px;
}

.carousel-img-m {
    height: 100%;
    object-fit: cover;
}

.type{
    background-color: #3D84FF;
    border-radius: 10px;
    color: white;
    font-family:Gilroy-Light;
    font-size: 10px;
    letter-spacing: 2px;
    margin-right: 20px;
    margin-top: 20px;
    padding:5px 0px 2px 0px;
    position: absolute;
    right: 0;
    text-align: center;
    text-transform: uppercase;
    top: 0;
    width: 70px;
}

.hour{
    color: #9D9BB6;
    font-weight: bold;
}

.text-event{
    color: #192452;
    font-weight: bold;
    font-size: 18px;
}

.carousel-text-left{
    font-size: 30px;
}

